.gotoorders {
  background: #9DD458;
  width: 100%;
  padding: 18px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  color: white;
  font-size: 16px;
  font-weight: 600;
  line-height: 19px;
  letter-spacing: 0em;
  text-align: center;
  align-items: center;
  border-radius: 18px;
  transition: 0.3s ease;
  text-decoration:none;
}
.gotoorders:hover {
  gap: 25px;
}
.orderPage {
  padding: 0 15px;
  &__hr {
    margin: 30px 0;
    height: 3px;
    background: #d0d0d0;
    border-radius: 100px;
    width: 100%;
    content: '';
    display: block;
  }
  &__prices {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  &__text {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    max-width: 240px;
    font-size: 20px;
    & span {
    }
  }
  &__footer {
    display: flex;
    justify-content: space-between;
  }
  &__delete {
    & button {
      padding: 20px 20px;
      font-size: 20px;
      background: #ff3131;;
    }
    & a {
      text-decoration: none;
    }
  }
}
@media (max-width: 460px) {
  .orderPage {
    &__text {
      max-width: 100%;
    }
    &__footer {
      gap: 40px;
      flex-direction: column;
    }
  }
}
