

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
}

body, html {
  font-size: 14px;
  line-height: 17px;
}
button {
  border: none;
  background: none;
  cursor: pointer;
}
.container {
  padding: 0 15px;
}
.App {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding-top: 120px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
